<template>
  <div>
    <form action="/login" method="get" id="login">
      <input
        v-model.trim="userID"
        type="text"
        name="userID"
        class="key"
        placeholder=" 账号"
      />
      <br />
      <input
        v-model.trim="password"
        type="password"
        name="password"
        class="key"
        placeholder=" 密码"
      />
      <br />
      <img @click="changeImg" id="verifyCodeImg" :src="verifyCodeImg" />
      <input
        v-model.trim="verifyCodeStr"
        id="verifyCodeStr"
        type="text"
        placeholder="请输入验证码"
      />
      <br />
      <span style="color: red">{{ error }}</span>
      <button @click="login" id="submit" type="button">登录</button>
    </form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      userID: "",
      password: "",
      verifyCodeStr: "",
      verifyCodeImg: "http://localhost:8080/login/verifyCodeImg",
      error: "",
    };
  },
  methods: {
    ...mapActions("group", ["initHost"]),
    changeImg() {
      //重新申请验证码
      //防止浏览器缓存不刷新
      this.verifyCodeImg =
        "http://localhost:8080/login/verifyCodeImg?t=" + Date.now();
    },
    login() {
      //第一步 首先判断 账号 密码 验证码 是否填写齐全
      if (!this.userID || !this.password) {
        alert("请输入账号密码");
        return;
      } else if (!this.verifyCodeStr) {
        alert("请输入验证码");
        return;
      }
      //第二步 发送网络请求 确定验证码正确
      this.$axios
        .get("http://localhost:8080/login/verifyCodeStr", {
          params: {
            verifyCodeStr: this.verifyCodeStr,
          },
        })
        .then((res) => {
          console.log(res);
          //res.data返回true | false
          if (!res.data) {
            //验证码错误
            this.error = "验证码输入错误,请重新输入";
            //更改验证码图片
            this.changeImg();
          } else {
            //验证码通过,继续发送登录请求
            this.$axios
              .get("http://localhost:8080/login", {
                params: {
                  userID: this.userID,
                  password: this.password,
                },
              })
              .then((res) => {
                //账号密码错误
                if (!res.data || !res.data.userID) {
                  this.error = "输入的密码和账号错误,请重新输入";
                  //更改验证码图片
                  this.changeImg();
                } else {
                  //检查该用户是否已经登录
                  if (res.data.userID === "0") {
                    this.error = "该用户已经登录,请勿重复登录";
                    return;
                  }
                  //验证成功
                  this.initHost(res.data);
                  //跳入群组页面
                  //群组页面应该配置前置路由，防止无账号密码登录,故在本地缓存记录登录信息
                  localStorage.setItem("login", "true");
                  localStorage.setItem("host", JSON.stringify(res.data));
                  this.$router.replace({
                    name: "group",
                  });
                }
              })
              //验证账号密码发生错误
              .catch((res) => {
                this.error = "请求发生异常,请检查网络环境";
              });
          }
        })
        //验证验证码发生错误
        .catch((res) => {
          this.error = "请求发生异常，请检查网络环境";
        });
    },
  },
};
</script>

<style scoped>
@font-face {
  font-family: "icomoon";
  src: url("/font/fonts/icomoon.eot?h5j9ar");
  src: url("/font/fonts/icomoon.eot?h5j9ar#iefix") format("embedded-opentype"),
    url("/font/fonts/icomoon.ttf?h5j9ar") format("truetype"),
    url("/font/fonts/icomoon.woff?h5j9ar") format("woff"),
    url("/font/fonts/icomoon.svg?h5j9ar#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
body {
  background-color: #efefef;
}

input {
  border: 0;
  border-bottom: 1px solid #e5e5e5;
  outline: none;
}

#login {
  box-sizing: border-box;
  width: 425px;
  height: 325px;
  margin: 100px auto;
  text-align: center;
  padding-top: 100px;
  background-color: #fff;
}

#login .key {
  height: 30px;
  width: 260px;
  margin-bottom: 10px;
  font-family: icomoon;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
}

#login #submit {
  height: 30px;
  width: 260px;
  border: 0;
  border-radius: 2px;
  background-color: #12b7f5;
  line-height: 30px;
  text-align: center;
  color: #fff;
}
#verifyCodeImg {
  width: 160px;
  height: 50px;
  background-color: pink;
  vertical-align: middle;
  margin-bottom: 10px;
}
#verifyCodeStr {
  margin-left: 10px;
  width: 85px;
  height: 15px;
}
</style>